<template>
  <div id="app">
    <div class="input">
      <input type="text" name="code" v-model="code">
      <button type="button" name="button" @click="">show</button>
      <p style="margin-top:50px">isSafari && 1: {{isSafari}}</p>
      <p style="margin-top:50px">isQQBrowser: {{isQQBrowser}}</p>
      <p style="margin-top:50px">Agent: {{Agent}}</p>
      <p style="margin-top:50px">IOS 版本为: {{version}}</p>
    </div>
    <a class="testBtn" href="itms-apps://itunes.apple.com/app/id544673497" @click.stop="open(appInfo)">
      打开app
    </a>
    <openApp @saveToCloud="saveToCloud" @goApp="goApp(appInfo)"/>

  </div>
</template>

<script type="text/javascript">
import openApp from './components/openApp.vue'
import { _openApp } from './utils/app'
// import {_myapp} from './utils/my'
import { isWeiXins } from './utils/my'
  export default {
    data() {
      return {
        code: '',
        input: 'iframe123-origin',
        appInfo: '',
        Agent: navigator.userAgent,
        isSafari: /Safari/.test(navigator.userAgent) && (!/Chrome/.test(navigator.userAgent))&&(!/QQBrowser/.test(navigator.userAgent))&&(!/FxiOS/.test(navigator.userAgent))&&(!/CriOS/.test(navigator.userAgent)),
        iosUrl: `mcloud://outLinks?outlink=`,
        version:'',
        isQQBrowser: /qqbrowser/.test(navigator.userAgent.toLowerCase()),
        downLoadUrl: 'itms-apps://itunes.apple.com/app/id544673497'
      }
    },
    components: {
      openApp
    },
    computed: {
    },
    computed: {
    },
    mounted() {
      var goAppInfo = {
        link: `http://caiyun.feixin.10086.cn/dl/185CjzcOjFTwe`,
        pwd: 'UlndMd',
        category: '00019700101000000001/1811hjJdT00Z05620191017152733yik',
        account: '19802021498',
        name: 'picture'
      }
      let jsonInfo = JSON.stringify(goAppInfo)
      let base64Info = window.btoa(jsonInfo)
      this.appInfo = base64Info
      let str= navigator.userAgent.toLowerCase()
      let ver=str.match(/cpu iphone os (.*?) like mac os/)
      console.log(ver);
      this.version = ver ? ver[1].replace(/_/g,".") : null
    },
    methods: {
      sleep(interval) {
        return new Promise(resolve => {
          setTimeout(resolve, interval);
        })
      },
       open(param) {
         let ua = navigator.userAgent
         let isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1,
           isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
           // if(isiOS) {
             var ifr = document.createElement('iframe')
             ifr.src = `mcloud://outLinks?outlink=${param}`
             // ifr.src = `http://fuguiguan.cn/cards`
             ifr.style.display = 'none'
             document.body.appendChild(ifr)
             setTimeout(function(){
              document.body.removeChild(ifr);
            }, 3000)
          // }else {
           //   alert('not ios1')
           // }
      },
      saveToCloud() {
        alert('saveToCloud-(去掉toLower函数)')
        console.log('cloud--log');
      },
       isQQ(){
        return navigator.userAgent.toLowerCase().includes("qqbrowser")
      },
      async _app(param) {
        _openApp(param)
      },
      async goApp(param) {
        var goAppInfo = {
          link: `http://caiyun.feixin.10086.cn/dl/185CjzcOjFTwe`,
          pwd: 'UlndMd',
          category: '00019700101000000001/1811hjJdT00Z05620191017152733yik',
          account: '19802021498',
          name: 'picture'
        }
        let jsonInfo = JSON.stringify(goAppInfo)
        let base64Info = window.btoa(jsonInfo)
        this.appInfo = base64Info
        let ua = navigator.userAgent
        let isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1,
          isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
        if(isWeiXins()) {
          alert("请点击右上角用浏览器查看！")
          return
        } else {
          if(isiOS) {
            if(this.isSafari) {
              this.$router.push({
                path: '/openapp',
                query: {
                  base64Info: base64Info
                }
              })
            } else {
              location.href = `mcloud://outLinks?outlink=${param}`
              await this.sleep(2000) // 休眠2s
              setTimeout(() => {
                if(!(document.hidden || document.webkitHidden)) {
                  // 唤起失败
                  location.href='itms-apps://itunes.apple.com/app/id544673497'
                }
              },2200)
            }
          } else if(isAndroid) {
            console.log('android');
            location.href = `hecaiyun://launch//outlinks?outlink=${base64Info}`
            await this.sleep(2000) // 休眠2s
            setTimeout(() => {
              if(!(document.hidden || document.webkitHidden)) {
                // 唤起失败
                location.href='http://caiyun.feixin.10086.cn:7070/portal/client_new.jsp?v=mCloud_800'
              }
            },2200)
          } else {
              console.log('sswsasaq');
            alert("DEMO，请在移动端的浏览器查看！")
            return
          }
        }
      }
    }
  }
</script>
<style>
  .testBtn {
    width: 100px;
    height: 40px;
    line-height: 40px;
    font-size: 18px;
    border: 1px solid blue;
    border-radius: 10px;
    text-align: center;
    background-color: green;
    color: #fff;
    box-shadow: 0 6px 12px rgba(94,136,155,0.8);
    text-decoration: none;
  }
</style>



