<!--唤起app中间页面 -->
<template>
    <div class="container">
        <div class="header">
            <div class="header-logo"></div>
            <div class="header-openapp" @click="open(base64Info)">点击打开</div>
        </div>
        <div class="main" @click="download">
        </div>
    </div>
</template>

<script>
export default {
    data() {
      return {
        base64Info: ''
      }
    },
    mounted() {
      let base64Info = this.$route.query.base64Info
      console.log(`from router ${base64Info}`);
      this.base64Info = base64Info
    },
    methods: {
        open(param) {
            console.log('open');

            location.href = `mcloud://outLinks?outlink=${param}`
        },
        download() {
            console.log('download');

            location.href = `itms-apps://itunes.apple.com/app/id544673497`
        }
    }
}
</script>

<style scoped>
    .container {
        background-image: url('../static/imgs/bg.png');
        width: 100vw;
        height: 100vh;
        background-size: 100%;
        background-repeat: no-repeat;
    }
    .header {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 40px;
        display: flex;
        display: -webkit-flex;
        display: -ms-flexbox;
        align-items: center;
    }
    .header-logo {
        background-image: url('../static/imgs/top_logo.png');
        background-size: 100%;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
    }
    .header-openapp {
        position: absolute;
        height: 40px;
        line-height: 40px;
        right: 15px;
        color: #5e88ff;
        text-align: center;
        font-size: 16px
    }
    .main {
        position: relative;
        width: 240px;
        height: 60px;
        z-index: 10;
        top: 52%;
        left: 50%;
        transform: translate(-120px,-25px);
        opacity: 0;
        background-color: #cef5e0;
    }
</style>



